
import axios from 'axios';
import { useState, useEffect} from 'react'
import MovieCard from '../components/movia-card/moviacard'
import './pages.scss'




const MovieLatest = () => {

  const [ movieLetset, setMovieLetset ] = useState({
    isFetched:false,
    data:{},
    error:null

  })
  // console.log(data);

  useEffect(() => {
  axios.get('https://api.themoviedb.org/3/movie/latest', {
    params: {
      api_key: "6f182a77a9989da5eb89e4e1e8a6b379"
    }
  })

  
  .then(function (response) {
    console.log(response);
    setMovieLetset ({
    isFetched: true,
    data: response.data,
    error: false
    })
  })
  .catch(function (error) {
    setMovieLetset ({
      isFetched:true,
      data:[],
      error:error
      })
    })
  }, []);
  
  
console.log(movieLetset);
    return ( 
      <div className="container">
      {
        movieLetset.isFetched ? (
          <div className="movie-list">
          <div className="film-information">
                 <div className="images">
                 {/* <img src={`https://image.tmdb.org/t/p/w500${}`} alt="film" className="film-img"/> */}
                 </div>
                  <div className="right-div">
                     <h1 className="film-name">{movieLetset.title}</h1>
                     {/* <h2 className="film-conpany">Conpany: <span className="spane">{movieInfo.data.production_companies[0].name}</span></h2> */}
                     
                     <h2 className="film-popularty">Year: <span className="spane">{}</span></h2>
                      <p className="film-title">{}</p>
                </div>
              </div>
          </div>
        ) : (
          <h1>loading...</h1>
        )
      }
    </div>
     );
}


 
export default MovieLatest;