import './main.scss'
import Header from './containers/header-containers'
import Footer from './containers/footer-container'

import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";

import {
  BrowserRouter as Router,
  Switch,
  Route
} from 'react-router-dom'

import {
  Home,
  TvShovs,
  Movies,
  SingleMovie,
  SingleShov,
  Highscore,
  MovieLatest,
  Movieget,
  PersonMovie,
  SearchResults,

} from './page';


function App() {
  return (
    <div className="App">
      <Router>
        <Header/>

        <div className="appBlock">
        <Switch>
          <Route exact path="/" component={Home}/>
          <Route exact path="/movies" component={Movies}/>
          <Route exact path="/Movie-Latest" component={MovieLatest}/>
          <Route exact path="/Movie-get" component={Movieget}/>
          <Route exact path="/Movie-get/:id" component={Movieget}/>
          <Route exact path="/High-Score" component={Highscore}/>
          <Route exact path="/tv-shows" component={TvShovs}/>
          <Route exact path="/movie/:id" component={SingleMovie}/>
          <Route exact path="/tv-show/:id" component={SingleShov}/>
          <Route exact path="/High-Score/:id" component={SingleShov}/>
          <Route exact path="/person/:id" component={PersonMovie}/>
          <Route exact path="/search/:searchQuery" component={SearchResults}/>
        </Switch>
        </div>

        <Footer/>
      </Router>
    </div>
  );
}

export default App;
