import {useEffect, useState} from 'react'
import axios from 'axios'
import MovieCard from '../components/movia-card'

const SearchResults = ({ match }) => {

    const [ movieList, SetMovieList ] = useState({
    isFetched:false,
    data:{},
    error:null

  })

useEffect(() => {
        axios.get(`https://api.themoviedb.org/3/search/movie?query=${match.params.searchQuery}`, {
    params: {
      api_key: "6f182a77a9989da5eb89e4e1e8a6b379"
    }
  })

  
  .then(function (response) {
    SetMovieList ({
    isFetched: true,
    data: response.data,
    error: false
    })
  })
  .catch(function (error) {
    SetMovieList ({
      isFetched:true,
      data:[],
      error:error
      })
    })
}, [match.params.searchQuery])


    return ( 
        <div className="container">
          {
            movieList.isFetched ? (
              <div className="movie-list">
                {
                    movieList.data.results.length > 0 ? (
                        movieList.data.results.map((movie, index) => (
                        <MovieCard
                            title={movie.title}
                            id={movie.id}
                            type="movie"
                            key={index}
                            rating={movie.vote_average}
                            relisedate={movie.release_date}
                            imglink={`https://image.tmdb.org/t/p/w500${movie.poster_path}`}
                        />
                  ))
                    ) : (
                      <div class="spinner"></div>
                    )
                }
              </div>
            ) : (
              <></>
            )
          }
        </div>
     );
}
 
export default SearchResults;