


import axios from 'axios';
import { useState, useEffect} from 'react'
import MovieCard from '../components/movia-card'
import './pages.scss'




const TvShovs = ({match}) => {

  const [ simleTv, setSimleTv ] = useState({
    isFetched:false,
    data:{},
    error:null

  })

  useEffect(() => {
  axios.get(`https://api.themoviedb.org/3/tv/${match.params.id}/recommendations`, {
    params: {
      api_key: "6f182a77a9989da5eb89e4e1e8a6b379"
    }
  })

  
  .then(function (response) {
    setSimleTv ({
    isFetched: true,
    data: response.data,
    error: false
    })
  })
  .catch(function (error) {
    setSimleTv ({
      isFetched:true,
      data:[],
      error:error
      })
    })
  }, [match.params.id]);

  const [ movieList, setMovieList ] = useState({
    isFetched:false,
    data:{},
    error:null

  })
  // console.log(data);

  useEffect(() => {
  axios.get('https://api.themoviedb.org/3/tv/popular', {
    params: {
      api_key: "6f182a77a9989da5eb89e4e1e8a6b379"
    }
  })
  .then(function (response) {
    setMovieList ({
    isFetched: true,
    data: response.data,
    error: false
    })
  })
  .catch(function (error) {
    setMovieList ({
      isFetched:true,
      data:[],
      error:error
      })
    })
  }, [match.params.id]);
  
  

    return ( 
        <div className="container">
          {
            movieList.isFetched ? (
              <div className="movie-list">
                  {
                  movieList.data.results.map((movie, index) => (
                    <MovieCard
                      title={movie.original_name}
                      id={movie.id}
                      key={index}
                      rating={movie.vote_average}
                      type="tv-show"
                      relisedate={movie.first_air_date}
                      imglink={`https://image.tmdb.org/t/p/w500${movie.poster_path}`}
                    />
                  ))
                }
              </div>
            ) : (
              <div class="spinner"></div>
            )
          }
          <div className="recomen-tv">

          </div>
        </div>
     );
}


 
export default TvShovs;