

import axios from 'axios';
import { useState, useEffect } from 'react'
import MovieCard from '../components/movia-card/moviacard'
import './pages.scss'
import { Helmet } from "react-helmet";





const PersonMovie = ({ match }) => {

  const [movieList, setMovieList] = useState({
    isFetched: false,
    data: {},
    error: null

  })
  // console.log(data);

  useEffect(() => {
    axios.get(`https://api.themoviedb.org/3/person/${match.params.id}/movie_credits`, {
      params: {
        api_key: "6f182a77a9989da5eb89e4e1e8a6b379"
      }
    })


      .then(function (response) {
        setMovieList({
          isFetched: true,
          data: response.data.cast,
          error: false
        })
      })
      .catch(function (error) {
        setMovieList({
          isFetched: true,
          data: [],
          error: error
        })
      })
  }, [match.params.id]);

  console.log(movieList)
  return (




    <div className="container">
      <Helmet>
        <meta charSet="utf-8" />
        <title>Movie app</title>
        <link rel="icon"
          type="image/png" href="../images/icon/logotip.png" />
      </Helmet>
      {
        movieList.isFetched ? (
          <div className="movie-list">
            {
              movieList.data.map((movie, index) => (
                <MovieCard
                  title={movie.title}
                  id={movie.id}
                  key={index}
                  type="movie"
                  rating={movie.vote_average}
                  relisedate={movie.release_date}
                  imglink={`https://image.tmdb.org/t/p/w500${movie.backdrop_path}`}
                />
              ))
            }
          </div>
        ) : (
          <div class="spinner"></div>
        )
      }
    </div>
  );
}



export default PersonMovie;