import axios from 'axios';
import { useState, useEffect } from 'react'
import FilmInfo from '../components/film-info-card'
import MovieCard from '../components/movia-card'
import Actors from '../components/actors-card'
import Helmet from 'react-helmet'

import React, { Component } from "react";
import Slider from "react-slick";


const SingleMovie = ({ match }) => {

  const respo = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 3
  };

  const [movieInfo, setMovieInfo] = useState({
    isFetched: false,
    data: {},
    error: null

  })

  useEffect(() => {
    axios.get(`https://api.themoviedb.org/3/movie/${match.params.id}`, {
      params: {
        api_key: "6f182a77a9989da5eb89e4e1e8a6b379"
      }
    })


      .then(function (response) {
        setMovieInfo({
          isFetched: true,
          data: response.data,
          error: false
        })
      })
      .catch(function (error) {
        setMovieInfo({
          isFetched: true,
          data: [],
          error: error
        })
      })
  }, [match.params.id]);



  const [actorInfo, setActorInfo] = useState({
    isFetched: false,
    data: {},
    error: null

  })

  useEffect(() => {
    axios.get(`https://api.themoviedb.org/3/movie/${match.params.id}/credits`, {
      params: {
        api_key: "6f182a77a9989da5eb89e4e1e8a6b379"
      }
    })


      .then(function (response) {
        setActorInfo({
          isFetched: true,
          data: response.data,
          error: false
        })
      })
      .catch(function (error) {
        setActorInfo({
          isFetched: true,
          data: [],
          error: error
        })
      })
  }, [match.params.id]);


  const [simleFilm, setSimleFilm] = useState({
    isFetched: false,
    data: {},
    error: null

  })

  useEffect(() => {
    axios.get(`https://api.themoviedb.org/3/movie/${match.params.id}/recommendations`, {
      params: {
        api_key: "6f182a77a9989da5eb89e4e1e8a6b379"
      }
    })


      .then(function (response) {
        setSimleFilm({
          isFetched: true,
          data: response.data,
          error: false
        })
      })
      .catch(function (error) {
        setSimleFilm({
          isFetched: true,
          data: [],
          error: error
        })
      })
  }, [match.params.id]);

  const [singleVideo, setSingleVideo] = useState({
    isFetched: false,
    data: {},
    error: null

  })



  console.log(movieInfo.data)

  return (
    <>

      <div className="container single-list">
        <div className="tope">

          {
            movieInfo.isFetched && actorInfo.isFetched ? (

              <div className="block">
                <div className="film-information">
                  <div className="images">
                    <img src={`https://image.tmdb.org/t/p/original${movieInfo.data.poster_path}`} alt="film" className="film-img" />
                  </div>
                  <div className="right-div">
                    <h1 className="film-name">{movieInfo.data.title}</h1>
                    {/* <h2 className="film-conpany">Conpany: <span className="spane">{movieInfo.data.production_companies[0].name}</span></h2> */}
                    <Helmet>
                      <title>{movieInfo.data.title}</title>
                      <meta charSet="utf-8" />
                      <link rel="icon"
                        type="image/png" href="../images/icon/logotip.png" />
                    </Helmet>
                    <h2 className="film-popularty">Year: <span className="spane">{movieInfo.data.release_date}</span></h2>
                    <p className="film-title">{movieInfo.data.overview}</p>
                    <h3 className="actor-block" >Actors: {actorInfo.data.cast.map(item => (
                      <p className="actor-name">{item.name},</p>
                    ))}</h3>
                    <h3 className="actor-block two-block" >Producer: {actorInfo.data.crew.map(item => (<>
                      {item.job == 'Producer' ? (<p className="actor-name">{item.name},</p>) : (null)}</>
                    ))}</h3>
                  </div>
                </div>


                {/* <h1 className="actor-item">Actors</h1> */}
                <div className="actor-list">
                    <Helmet>
                      <title>actor-list</title>
                      <meta charSet="utf-8" />
                      <link rel="icon"
                        type="image/png" href="../images/icon/logotip.png" />
                    </Helmet>
                  {
                    actorInfo.data.cast.map((movie, index) => (
                      
                      <Actors
                        id={movie.id}
                        
                        images={
                          movie.profile_path !== null ? (
                            `https://image.tmdb.org/t/p/original${movie.profile_path}`
                          ) : (`https://image.tmdb.org/t/p/original${movie.profile_path}`)
                        }
                        title={movie.name}
                        key={index}
                        name={movie.name}
                      />
                    ))
                  }
                </div>
              </div>
            ) : (
              <div class="spinner"></div>
            )
          }
        </div>



        <div className="recom-block">
          <h2 className="rec-text">Recommendations</h2>
          <Slider {...respo}>
            {
              simleFilm.isFetched ? (

                simleFilm.data.results.map((movie, index) => (

                  <MovieCard
                    title={movie.title}
                    id={movie.id}
                    type="movie"
                    key={index}
                    rating={movie.vote_average}
                    relisedate={movie.release_date}
                    imglink={`https://image.tmdb.org/t/p/w500${movie.poster_path}`}

                  />

                ))

              ) : (
                <></>
              )
            }
          </Slider>




        </div>

      </div>

    </>
  );
}

export default SingleMovie;