import axios from 'axios';
import { useState, useEffect} from 'react'
import Highscorecard from '../components/highscore-card/highscorecard'
import './pages.scss'




const Highscore = () => {

  const [ movieList, setMovieList ] = useState({
    isFetched:false,
    data:{},
    error:null

  })

  useEffect(() => {
  axios.get('https://api.themoviedb.org/3/movie/top_rated', {
    params: {
      api_key: "6f182a77a9989da5eb89e4e1e8a6b379"
    }
  })

  
  .then(function (response) {
    setMovieList ({
    isFetched: true,
    data: response.data,
    error: false
    })
  })
  .catch(function (error) {
    setMovieList ({
      isFetched:true,
      data:[],
      error:error
      })
    })
  }, []);
  
  

    return ( 
        <div className="container">
          {
            movieList.isFetched ? (
              <div className="movie-list">
                {
                  movieList.data.results.map((movie, index) => (
                    <Highscorecard
                      title={movie.title}
                      id={movie.id}
                      key={index}
                      rating={movie.vote_average}
                      relisedate={movie.release_date}
                      imglink={`https://image.tmdb.org/t/p/w500${movie.backdrop_path}`}
                    />
                  ))
                }
              </div>
            ) : (
              <div class="spinner"></div>
            )
          }

          {/* <h1>high</h1> */}
        </div>
     );
}


 
export default Highscore;