import { Link }  from 'react-router-dom';
import './moviacard.scss'
import play from '../../images/icon/play.webp'

const Movicard = ({imglink, relisedate, title, rating, type, id, page}) => {
    return ( 
        <Link to={`/${type}/${id}`} className="movie-card">
            <span className="card-rating">{rating}</span>
            <div className="card-img-holder">
            <img src={imglink} alt="" className="movie-img"/>
            </div>
            <h2 className="card-title">{title}</h2>
            <h5 className="card-date">{relisedate}</h5>
            <img src={play} alt="icon" className="plays"/>
            <div className="shadov"></div>

        </Link>
     );
}
 
export default Movicard;