import axios from 'axios';
import { useState, useEffect} from 'react'
import MovieCard from '../components/movia-card/moviacard'
import './pages.scss'




const Movieget = () => {

  const [ Movieget, setMovieList ] = useState({
    isFetched:false,
    data:{},
    error:null

  })
  // console.log(data);

  useEffect(() => {
  axios.get('https://api.themoviedb.org/3/movie/upcoming', {
    params: {
      api_key: "6f182a77a9989da5eb89e4e1e8a6b379"
    }
  })

  
  .then(function (response) {
    setMovieList ({
    isFetched: true,
    data: response.data,
    error: false
    })
  })
  .catch(function (error) {
    setMovieList ({
      isFetched:true,
      data:[],
      error:error
      })
    })
  }, []);
  
  

    return ( 
        <div className="container">
          {
            Movieget.isFetched ? (
              <div className="movie-list">
              
                {
                  Movieget.data.results.map((movie, index) => (
                    <MovieCard
                      title={movie.title}
                      id={movie.id}
                      key={index}
                      type="Movie"
                      rating={movie.vote_average}
                      relisedate={movie.release_date}
                      imglink={`https://image.tmdb.org/t/p/w500${movie.poster_path}`}
                    />
                  ))
                }
              </div>
            ) : (
              <div class="spinner"></div>
            )
          }
        </div>
     );
}


 
export default Movieget;