import axios from 'axios';
import { useState, useEffect} from 'react'
import FilmInfo from '../components/film-info-card'
import Actors from '../components/actors-card'
import MovieCard from '../components/movia-card'

import React, { Component } from "react";
import Slider from "react-slick";

const SingleSHov = ({ match }) => {

  const tvSlide = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow:4,
    slidesToScroll: 3
  };

  const [ movieInfo, setMovieInfo ] = useState({
    isFetched:false,
    data:{},
    error:null

  })
  // console.log(data);

  useEffect(() => {
  axios.get(`https://api.themoviedb.org/3/tv/${match.params.id}`, {
    params: {
      api_key: "6f182a77a9989da5eb89e4e1e8a6b379"
    }
  })

  
  .then(function (response) {
    setMovieInfo ({
    isFetched: true,
    data: response.data,
    error: false
    })
  })
  .catch(function (error) {
    setMovieInfo ({
      isFetched:true,
      data:[],
      error:error
      })
    })
  }, [match.params.id]);

  const [ actorInfo, setActorInfo ] = useState({
    isFetched:false,
    data:{},
    error:null

  })

  useEffect(() => {
  axios.get(`https://api.themoviedb.org/3/tv/${match.params.id}/credits`, {
    params: {
      api_key: "6f182a77a9989da5eb89e4e1e8a6b379"
    }
  })

  
  .then(function (response) {
    setActorInfo ({
    isFetched: true,
    data: response.data,
    error: false
    })
  })
  
  .catch(function (error) {
    setActorInfo ({
      isFetched:true,
      data:[],
      error:error
      })
    })
  }, [match.params.id]);

  const [ simleTv, setSimleTv ] = useState({
    isFetched:false,
    data:{},
    error:null

  })

  useEffect(() => {
  axios.get(`https://api.themoviedb.org/3/tv/${match.params.id}/recommendations`, {
    params: {
      api_key: "6f182a77a9989da5eb89e4e1e8a6b379"
    }
  })

  
  .then(function (response) {
    setSimleTv ({
    isFetched: true,
    data: response.data,
    error: false
    })
  })
  .catch(function (error) {
    setSimleTv ({
      isFetched:true,
      data:[],
      error:error
      })
    })
  }, [match.params.id]);

  console.log(actorInfo);


    return ( 
        <div className="container single-list">
          {
            movieInfo.isFetched && actorInfo.isFetched ? (
              
              <div className="tv-shovs">
                <div className="film-information">
                  <div className="images">
                    <img src={`https://image.tmdb.org/t/p/w500${movieInfo.data.poster_path}`} alt="film" className="film-img"/>
                  </div>
                    <div className="right-div">
                      <h1 className="film-name">{movieInfo.data.name}</h1>
                      <h2 className="film-conpany">Conpany: <span className="spane">{movieInfo.data.production_companies[0].name}</span></h2>
                      
                      <h2 className="film-popularty">last_air_date: <span className="spane">{movieInfo.data.last_air_date}</span></h2>
                        <p className="film-title">{movieInfo.data.overview}</p>
                    </div>
              
                </div>

                <h1 className="actor-item">Actors</h1>
              <div className="actor-list">
                {
                  actorInfo.data.cast.map((movie, index) => (
                    <Actors
                      id={movie.id}
                      images={`https://image.tmdb.org/t/p/w500${movie.profile_path}`}
                      key={index}
                      title={movie.name}
                    />
                  ))
                }
              </div>
              </div>
            ) : (
              <div class="spinner"></div>
            )
          }

              <h2 className="rec-text">recommendations</h2>
               <div className="recom-block">
                    <Slider {...tvSlide}>
                  {
                    simleTv.isFetched ? (
                      simleTv.data.results.map((movie, index) => (
                      <div>
                      <MovieCard
                        title={movie.title}
                        id={movie.id}
                        type="tv-show"
                        key={index}
                        rating={movie.vote_average}
                        relisedate={movie.release_date}
                        imglink={`https://image.tmdb.org/t/p/w500${movie.poster_path}`}
                      />
                      </div>
                    ))
                    ) : (
                      <></>
                    )
                  }
                    </Slider>
               </div>
           
        </div>
     );
}
 
export default SingleSHov;