import './header-containers.scss'
import searchicon from '../../images/icon/search1.png'
import axios from 'axios';
import { useState, useEffect, useRef } from 'react'
import SearchItem from '../../components/search-item'

import { Link, useHistory } from 'react-router-dom'

import filmIcom from '../../images/icon/Film-icon.png'
import filmIcom1 from '../../images/icon/imdb.png'



const HeaderContainers = () => {
    let history = useHistory();
    const [elementClass, setElementClass] = useState(false)
    const [searchClass, setSearchClass] = useState(false)
    const [ movieList, SetMovieList ] = useState({
        isFetched:false,
        data:{},
        error:null
    
      })


    const [linkClick, setLinkClick] = useState('')

    const [searchValue, setSearchValue] = useState('')

    const getSearchetMovies = () => {
        axios.get(`https://api.themoviedb.org/3/search/movie?query=${searchValue}`, {
            params: {
                api_key: "6f182a77a9989da5eb89e4e1e8a6b379"
            }
        })


            .then(function (response) {
                SetMovieList({
                    isFetched: true,
                    data: response.data,
                    error: false
                })
            })
            .catch(function (error) {
                SetMovieList({
                    isFetched: true,
                    data: [],
                    error: error
                })
            })
    }

    useEffect(() => {
        if (searchValue.length > 0) {
            
            getSearchetMovies();
        } else {
            // history.push(`/`)
        }
    }, [searchValue]);

    const inputRef = useRef()
    const inputHandler = () => {
        inputRef.current.focus();
        setElementClass(!elementClass)
    }
    window.addEventListener('keypress', (e) => {
        if (e.key == 'Enter') {
            setElementClass(false)
            
        }
    })

    const nimadir = () => history.push(`/search/${searchValue}`)

    return (
        <div className="header">
            <div onClick={() => setElementClass(!elementClass)} className={`modal-input ${elementClass ? "activ" : " "}`}  >
            </div>
            <div className="header-inner">
                <div className="header-left">
                    <Link to="/" onClick={() => setLinkClick('/')} className={`header-logotip ${linkClick == '/' ? 'activ' : ''}`}>
                        <h2 className="logotip">
                            <span className="one-spane q">F</span>
                            <span className="one-spane w">I</span>
                            <span className="one-spane e">R</span>
                            <span className="one-spane r">S</span>
                            <span className="one-spane t">T</span>
                            <span className="two-spane y">M</span>
                            <span className="two-spane u">O</span>
                            <span className="two-spane i">V</span>
                            <span className="two-spane o">I</span>
                            <span className="two-spane p">E</span>

                        </h2>
                    </Link>
                </div>
                <div className="header-middle">
                    <Link to="/movies" onClick={() => setLinkClick('movies')} className={`header-link ${linkClick == 'movies' ? 'activ' : ''}`}>Movies</Link>
                    <Link to="/tv-shows" onClick={() => setLinkClick('tv-shws')} className={`header-link ${linkClick == 'tv-shws' ? 'activ' : ''}`}>Tv-shovs</Link>
                    <Link to="/High-Score" onClick={() => setLinkClick('High-Score')} className={`header-link ${linkClick == 'High-Score' ? 'activ' : ''}`}>High-Score</Link>
                    <Link to="/Movie-Get" onClick={() => setLinkClick('Movie-Get')} className={`header-link ${linkClick == 'Movie-Get' ? 'activ' : ''}`}>Movie-get</Link>
                </div>
                <div className="header-right">
                    <div >

                        <div className="input-div">
                            <form onSubmit={nimadir}>
                            <input ref={inputRef} onChange={(e) => setSearchValue(e.target.value)} value={searchValue} type="search" placeholder="Search" className={`input ${elementClass ? "activ" : " "}`} />
                            </form>
                            <div className={`search-result ${elementClass ? "activ" : " "}`}>
                                {
                                    movieList.isFetched ? (
                                        
                                        <div className="div">
                                            {
                                                movieList.data.results.length > 0 ? (
                                                    <div>
                                                        {
                                                            movieList.data.results.splice(0, 3).map((item, index) => (
                                                                <Link to={`/movie/${item.id}`} className="search-item">
                                                                    <div className="search-left">
                                                                        <div className="left-inner">
                                                                        <img className="film-photo" src={`https://image.tmdb.org/t/p/original${item.poster_path}`} alt=""/>
                                                                        </div>
                                                                        <div className="left-right">
                                                                            <h4 className="film-name" key={index}>{item.title}</h4>
                                                                            <p className="film-date">{item.release_date}</p>
                                                                        </div>
                                                                    </div>
                                                                    <div className="search-right">
                                                                        <img src={filmIcom} alt="" className="search-img"/>
                                                                        <img src={filmIcom1} alt="" className="search-img"/>
                                                                    </div>
                                                                </Link>
                                                            ))
                                                        }
                                                        <Link className="allResults" to={`/search/${searchValue}`}>All results</Link>
                                                    </div>
                                                ) : (
                                                    <h1>There is no such movie.</h1>
                                                    )
                                            }
                                        </div>
                                    ) : (
                                        <></>
                                    )
                                }
                            </div>
                        </div>
                        <a href="javascript:void(0)" onClick={() => inputHandler()} className="search" >
                            <svg className="search-svg" viewBox="0 0 20 20" fill="none" class="" width="20" height="20"><path fill-rule="evenodd" clip-rule="evenodd" d="M14.9469 13.7714L19.7551 18.5796C20.0816 18.902 20.0816 19.4327 19.7551 19.7551C19.4286 20.0816 18.902 20.0816 18.5755 19.7551L13.7673 14.951C12.3143 16.1429 10.4571 16.8571 8.42857 16.8571C3.77143 16.8571 0 13.0816 0 8.42857C0 3.77551 3.77143 0 8.42857 0C13.0816 0 16.8571 3.77551 16.8571 8.42857C16.8571 10.4571 16.1388 12.3184 14.9469 13.7714ZM1.67347 8.42857C1.67347 12.151 4.70204 15.1796 8.42857 15.1796C12.151 15.1796 15.1837 12.151 15.1837 8.42857C15.1837 4.70612 12.151 1.67347 8.42857 1.67347C4.70204 1.67347 1.67347 4.70612 1.67347 8.42857Z" fill="white"></path></svg>
                        </a>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default HeaderContainers;
