import HighScore from './Highscore'
import Highscorecard from '../components/highscore-card'
import React, { Component } from "react";
import Slider from "react-slick";
import MovieCard from '../components/movia-card'
import BigCaro from '../components/big-carousel'

import axios from 'axios';
import { useState, useEffect } from 'react'



const Home = () => {

  const [tvList, setTvList] = useState({
    isFetched: false,
    data: {},
    error: null

  })
  // console.log(data);

  useEffect(() => {
    axios.get('https://api.themoviedb.org/3/tv/popular', {
      params: {
        api_key: "6f182a77a9989da5eb89e4e1e8a6b379"
      }
    })
      .then(function (response) {
        setTvList({
          isFetched: true,
          data: response.data,
          error: false
        })
      })
      .catch(function (error) {
        setTvList({
          isFetched: true,
          data: [],
          error: error
        })
      })
  }, []);

  const [OnlineTv, setOnlineTv] = useState({
    isFetched: false,
    data: {},
    error: null

  })
  // console.log(data);

  useEffect(() => {
    axios.get('https://api.themoviedb.org/3/tv/airing_today', {
      params: {
        api_key: "6f182a77a9989da5eb89e4e1e8a6b379"
      }
    })
      .then(function (response) {
        setOnlineTv({
          isFetched: true,
          data: response.data,
          error: false
        })
      })
      .catch(function (error) {
        setOnlineTv({
          isFetched: true,
          data: [],
          error: error
        })
      })
  }, []);


  const [movieList, setMovieList] = useState({
    isFetched: false,
    data: {},
    error: null

  })

  useEffect(() => {
    axios.get('https://api.themoviedb.org/3/movie/top_rated', {
      params: {
        api_key: "6f182a77a9989da5eb89e4e1e8a6b379"
      }
    })


      .then(function (response) {
        setMovieList({
          isFetched: true,
          data: response.data,
          error: false
        })
      })
      .catch(function (error) {
        setMovieList({
          isFetched: true,
          data: [],
          error: error
        })
      })
  }, []);



  const [nowplaying, setNowPlaying] = useState({
    isFetched: false,
    data: {},
    error: null

  })

  useEffect(() => {
    axios.get('https://api.themoviedb.org/3/movie/now_playing', {
      params: {
        api_key: "6f182a77a9989da5eb89e4e1e8a6b379"
      }
    })


      .then(function (response) {
        setNowPlaying({
          isFetched: true,
          data: response.data,
          error: false
        })
      })
      .catch(function (error) {
        setNowPlaying({
          isFetched: true,
          data: [],
          error: error
        })
      })
  }, []);




  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true
  };

  const tvCaro = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 3
  };

  const onlineCaro = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 3
  };

  const nowPlaying = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 3
  };

  return (
    <div className="home-page">
      <div className="tv-block">
        <Slider {...settings} className="big-carousel">
          {
            movieList.isFetched ? (
              movieList.data.results.map((movie, index) => (
                <BigCaro
                  // title={movie.title}
                  id={movie.id}
                  key={index}
                  type="movie"
                  // rating={movie.vote_average}
                  // relisedate={movie.release_date}
                  img={`https://image.tmdb.org/t/p/original${movie.backdrop_path}`}
                />
              ))
            ) : (
              <div class="spinner"></div>
            )

          }
        </Slider>
      </div>

      <div className="tv-block container">
        <h1 className="tv-text">TV series</h1>
        <Slider {...tvCaro}>
          {
            tvList.isFetched ? (
              tvList.data.results.map((movie, index) => (
                <MovieCard
                  title={movie.original_name}
                  id={movie.id}
                  key={index}
                  rating={movie.vote_average}
                  type="tv-show"
                  relisedate={movie.first_air_date}
                  imglink={`https://image.tmdb.org/t/p/original${movie.poster_path}`}
                />
              ))
            ) : (
              <></>
            )
          }
        </Slider>
      </div>

      <div className="tv container">
        <h1 className="tv-text">TV Airing Today</h1>
        <Slider {...onlineCaro}>
          {
            OnlineTv.isFetched ? (
              OnlineTv.data.results.map((movie, index) => (
                <MovieCard
                  title={movie.original_name}
                  id={movie.id}
                  key={index}
                  rating={movie.vote_average}
                  type="tv-show"
                  relisedate={movie.first_air_date}
                  imglink={`https://image.tmdb.org/t/p/original${movie.poster_path}`}
                />
              ))
            ) : (
              <></>
            )
          }
        </Slider>
      </div>


      <h1 className="tv-text container">
        Now Playing
        </h1>
      <div className="tv-block container">
        <Slider {...nowPlaying}>
          {
            nowplaying.isFetched ? (
              nowplaying.data.results.map((movie, index) => (
                <MovieCard
                  title={movie.title}
                  id={movie.id}
                  key={index}
                  type="movie"
                  rating={movie.vote_average}
                  relisedate={movie.release_date}
                  imglink={`https://image.tmdb.org/t/p/original${movie.backdrop_path}`}
                />
              ))
            ) : (
              <div class="spinner"></div>
            )

          }
        </Slider>
      </div>


    </div>
  );
}

export default Home;