import './actors.scss'
import { Link }  from 'react-router-dom';

const Actors = ({images, name, id}) => {
    return ( 
        <Link to={`/person/${id}`} className="actors-card">
            <div className="actors">
                
                <img src={images} alt="rasm" className="actor-img"/>
                <h3 className="actor-name">{name}</h3>
            </div>

        </Link>
     );
}
 
export default Actors;